import React, { PureComponent } from 'react';
import { Formik } from 'formik';
import { withTranslation } from 'react-i18next';
import faqIcon from '../../assets/img/faqIcon.png';
import { t } from '../../i18n';
import { FAQ_NAMESPACE } from './constants';
import FAQQuestion from '../../components/FAQQuestion/FAQQuestion';
import { FAQList } from './constants';

class FAQ extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      searchValues: this.props.location.state
        ? this.props.location.state.searchValues
        : [],
    };
  }
  _renderFAQList = () =>
    FAQList.map(item => (
      <>
        <h3>{item.category}</h3>
        <div className="list-accordion">
          {item.questions.map((subItem, index) => (
            <FAQQuestion
              key={subItem.question}
              questionNumber={index + 1}
              question={subItem.question}
              answer={subItem.answer}
              searchValues={this.state.searchValues}
            />
          ))}
        </div>
      </>
    ));
  componentDidMount() {
    window.scrollTo(0, 0);
  }
  render() {
    const initialSearchValue = {
      searchValue: this.props.location.state
        ? this.props.location.state.searchValues.join(' ')
        : '',
    };
    return (
      <div className="block-faq">
        <img src={faqIcon} alt="" />
        <div className="container">
          <h2> {t(`${FAQ_NAMESPACE}haveQuestions`)}</h2>
          <h3>{t(`${FAQ_NAMESPACE}checkoutOurPlatform`)}</h3>
          <Formik
            initialValues={initialSearchValue}
            onSubmit={(values, { setSubmitting }) => {
              setSubmitting(true);
              if (values.searchValue)
                this.setState(
                  {
                    searchValues: values.searchValue
                      .trim()
                      .toLowerCase()
                      .split(' '),
                  },
                  () => {
                    setSubmitting(false);
                  },
                );
            }}
          >
            {({ handleSubmit, values, handleChange, handleBlur }) => (
              <form onSubmit={handleSubmit}>
                <div className="form-group">
                  <input
                    name="searchValue"
                    className="form-control"
                    autoComplete="off"
                    type="text"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.searchValue}
                  />
                </div>
                <div className="form-group">
                  <button className="btn btn-primary btn-sm" type="submit" />
                </div>
              </form>
            )}
          </Formik>
          <div className="list-faq">{this._renderFAQList()}</div>
        </div>
      </div>
    );
  }
}

export default withTranslation()(FAQ);
