import React from 'react';

class FAQQuestion extends React.PureComponent {
  state = { shownAnswer: false };
  _searchValueFound = false;
  _answer = null;
  _question = null;
  _toggleAnswer = () => {
    this.setState({ shownAnswer: !this.state.shownAnswer });
  };
  _buildQuestion = () =>
    this.props.question.split(' ').map(word => {
      if (
        this.props.searchValues &&
        this.props.searchValues.includes(word.toLowerCase())
      ) {
        return (
          <>
            <span style={{ 'background-color': 'yellow' }}>{`${word}`}</span>
            {` `}
          </>
        );
      }
      return <>{`${word} `}</>;
    });
  _buildAnswer = () =>
    this.props.answer.split('\n').map(sentence => {
      const sentenceArray = sentence.split(' ').map(word => {
        if (
          this.props.searchValues &&
          this.props.searchValues.includes(word.toLowerCase())
        ) {
          this._searchValueFound = true;
          return (
            <>
              <span style={{ 'background-color': 'yellow' }}>{`${word}`}</span>
              {` `}
            </>
          );
        }
        return <>{`${word} `}</>;
      });
      return (
        <>
          {sentenceArray}
          <br />
        </>
      );
    });
  componentDidUpdate(prevProps, prevState, snapShot) {
    if (prevProps.searchValues !== this.props.searchValues) {
      this.setState({ shownAnswer: this._searchValueFound }, () => {
        this._searchValueFound = false;
      });
    }
  }
  componentDidMount() {
    if (this._searchValueFound) {
      this.setState({ shownAnswer: true });
    }
  }
  render() {
    this._answer = this._buildAnswer();
    this._question = this._buildQuestion();
    const itemClassName = `item ${this.state.shownAnswer ? 'active' : ''}`;
    return (
      <div className={itemClassName}>
        <h4 className="itemHeader" onClick={this._toggleAnswer}>
          {`${this.props.questionNumber} - `}
          {this._question}
        </h4>
        <div className="contente">
          <p>{this._answer}</p>
        </div>
      </div>
    );
  }
}

export default FAQQuestion;
